import { useTranslation } from "react-i18next";
import { useInstituteAllMixedDepartment } from "../../Hooks/ApiHooks/SiteManager/site-manager-api";
import CustomSelectDepartment from "../../Ui/Select/CustomSelectDepartment";
import Loading from "../../Loader/Loading";

const InstituteMixedDepartmentDropdown = ({
  instituteId,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
  selectLabel,
}) => {
  const { t } = useTranslation();
  const { instituteAllMixedDepartment, instituteAllMixedDepartmentLoading } =
    useInstituteAllMixedDepartment({
      data: {
        id: instituteId,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !instituteId,
    });
  return (
    <>
      <CustomSelectDepartment
        selectLabel={selectLabel ?? t("select_department")}
        selectedValue={t("select_department_placeholder")}
        options={
          instituteAllMixedDepartment?.list?.length > 0
            ? instituteAllMixedDepartment?.list
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        viewAs="NORMAL_FILTER"
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
        customListStyle={{
          position: "absolute",
          width: "100%",
          top: "100%",
          zIndex: "1",
        }}
      />

      {/* {instituteAllMixedDepartmentLoading && <Loading />} */}
    </>
  );
};

export default InstituteMixedDepartmentDropdown;
