import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import QvipleLoader from "../../optimized/Loader/QvipleLoader";
import BorderBottom from "../../optimized/Ui/Border/BorderBottom";
import CustomCheckbox from "../../optimized/Ui/CustomCheckbox/CustomCheckbox";
import { Fragment, useEffect, useState } from "react";
import FormFile from "../../optimized/Ui/Input/FormFile";
import FormCalendarInput from "../../optimized/Ui/Input/FormCalendarInput";
import CustomRadioButton from "../../optimized/Ui/CustomRadioButton/CustomRadioButton";
import CustomSelect from "../../optimized/Ui/Select/CustomSelect";
import FormTextarea from "../../optimized/Ui/Input/FormTextarea";
import Loading from "../../optimized/Loader/Loading";
import { useFileUpload } from "../../hooks/authentication-api";
import { useStudentEditProfileAboutDetail } from "../../optimized/Hooks/ApiHooks/InstituteStudent/institute-student-api";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../optimized/Utils/Functions/dateUtility";
import style from "../../optimized/Form/Form.module.css";
import FormInput from "../../optimized/Ui/Input/FormInput";
import FormCreateButton from "../../optimized/Ui/Button/FormCreateButton";
import { useStudentFormEdit } from "../../hooks/member_tab/class-api";
import {
  existingRequiredField,
  requiredField,
} from "../../optimized/Validation/functions/checkingRequiredField";
import { useStudentReadmissionFee } from "../../optimized/Hooks/ApiHooks/StudentProfile/student-manager-api";
import StudentAdmissionPayFees from "../../optimized/UserStaffMember/StudentAdmissionFees/PayFees/StudentAdmissionPayFees";
import { assestsFeedUrl } from "../../optimized/Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../optimized/Services/UrlConfig/BaseUrl";
import { Dialog } from "@mui/material";
import CustomCrop from "../../ImageCrop/CustomCrop";
const SelfStudentEditDynamicForm = () => {
  const getQuery = useLocation();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [stateLabel, setStateLabel] = useState([]);
  const [state, setState] = useState({});
  const [dynamicState, setDynamicState] = useState({});
  const [notRequiredState, setNotRequiredState] = useState([]);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [admission, setAdmission] = useState("");

  const [fileUpload] = useFileUpload();
  const [studentFormEdit] = useStudentFormEdit();
  const [studentReadmissionFee] = useStudentReadmissionFee();
  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");
  const [activeStep, setActiveStep] = useState("");

  const {
    studentEditProfileAboutDetail,
    studentEditProfileAboutDetailRefetch,
  } = useStudentEditProfileAboutDetail({
    data: {
      sid: getQuery.state?.sid,
    },
    skip: !getQuery.state?.sid,
  });

  // console.info("studentEditProfileAboutDetail", studentEditProfileAboutDetail);

  useEffect(() => {
    if (studentEditProfileAboutDetail) {
      let lb = [];
      let k_lb = {};
      let dy = {};
      let rq = ["student_ph_type"];
      for (let i = 0; i < studentEditProfileAboutDetail?.length; i++) {
        let st = studentEditProfileAboutDetail[i];
        let obj = {
          section_name: st?.key,
          section_key: st?.static_key,
          field_list: [],
          nested_section: st?.nested_section,
        };
        for (let j = 0; j < st?.fields?.length; j++) {
          let ct = st?.fields[j];
          obj.field_list?.push({
            label: ct?.form_checklist_lable,
            placeholder: ct?.form_checklist_placeholder,
            type: ct?.form_checklist_typo,
            db_key: ct?.form_checklist_key,
            options: ct?.form_checklist_typo_option_pl,
            width: ct?.width,
            nested_form_checklist: ct?.nested_form_checklist ?? [],
            form_common_key: ct?.form_common_key,
          });
          for (let dt of ct?.nested_form_checklist ?? []) {
            if (dt?.form_checklist_key)
              k_lb[dt?.form_checklist_key] = dt?.value ?? "";
            if (
              ["std_tenth_details", "hsc_diploma"]?.includes(
                ct?.form_checklist_key
              )
            ) {
              if (!ct?.form_checklist_required) {
                rq.push(dt?.form_checklist_key);
              }
            } else {
              if (!dt?.form_checklist_required) {
                rq.push(dt?.form_checklist_key);
              }
            }
            if (dt?.form_checklist_key_status === "DYNAMIC") {
              dy[dt?.form_checklist_key] = "";
            }
            for (let ht of dt?.nested_form_checklist_nested) {
              if (ht?.form_checklist_key)
                k_lb[ht?.form_checklist_key] = ht?.value ?? "";
              if (!ht?.form_checklist_required) {
                rq.push(ht?.form_checklist_key);
              }
              if (ht?.form_checklist_key_status === "DYNAMIC") {
                dy[ht?.form_checklist_key] = "";
              }
            }
          }
          if (ct?.form_checklist_key) k_lb[ct?.form_checklist_key] = ct?.value;
          if (ct?.form_checklist_key_status === "DYNAMIC") {
            dy[ct?.form_checklist_key] = ct?.value;
          }
          if (!ct?.form_checklist_required) {
            if (ct?.form_checklist_key === "student_signature") {
            } else {
              rq.push(ct?.form_checklist_key);
            }
          }
        }
        lb.push(obj);
        // if (st?.static_key?.section_key === "academic_details") {
        //   for (let th of st?.fields) {
        //     rq.push(th?.form_checklist_key);
        //   }
        // }
      }

      setStateLabel(lb);
      setState(k_lb);
      setDynamicState(dy);
      setNotRequiredState(rq);
    }
  }, [studentEditProfileAboutDetail]);

  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };

  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [activeStep]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onCropFileUpload = (e, flow) => {
    if (e.target.files[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
      setActiveStep(flow);
    }
  };

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onNumberInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };
  const onAcademicCheckbox = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e.target.checked ? "Yes" : "No",
    }));
  };

  const onUploadDocument = (e, flow) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [flow]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onCheckbox = (e, flow) => {
    if (
      [
        "student_asm_wasm_check",
        "student_ddww_check",
        "student_esm_wasm_check",
        "student_ff_wff_check",
        "student_km_check",
        "student_moeaf_check",
        "student_moffaf_check",
        "student_mopaf_check",
        "student_rota_check",
        "student_wpt_check",
        "student_wst_check",
      ]?.includes(flow)
    ) {
      setState((prev) => ({
        ...prev,
        [flow]: e.target.checked ? "Yes" : "No",
      }));
      let dfg = notRequiredState?.filter(
        (df) => df !== flow?.substring(0, flow?.length - 6)
      );
      setNotRequiredState(dfg);
    } else {
      setState((prev) => ({
        ...prev,
        [flow]: e.target.checked,
      }));
    }
  };
  const onCalendarChoose = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(e),
    }));
  };

  const onPreview = (e) => {
    e.preventDefault();
    let validation = requiredField(state, notRequiredState);
    if (existingRequiredField(validation) && getQuery.state?.sid) {
      setDisabled((pre) => !pre);
      let editProfile = {};
      if (
        getQuery.state?.extraData?.whichAuthorized === "SELF_EDIT_APPLICATION"
      ) {
        editProfile = {
          student: { ...state },
          fileArray: [],
          student_dynamic_field: [],
          ...getQuery.state?.extraData,
        };
      } else {
        editProfile = {
          student: { ...state },
          fileArray: [],
          student_dynamic_field: [],
        };
      }

      for (let ob in dynamicState) {
        editProfile.student_dynamic_field.push({
          key: ob,
          value: state[ob],
        });
      }

      studentFormEdit({
        sid: getQuery.state?.sid,
        editProfile,
      })
        .then(() => {
          if (getQuery.state?.type === "READMISSION") {
            studentReadmissionFee({
              fid: getQuery.state?.student?.fee_structure,
              sid: getQuery.state?.student?._id,
            })
              .then((res) => {
                if (res.data?.remain_card) {
                  setAdmission(res.data?.remain_card);
                  setDisabled((pre) => !pre);
                }
              })
              .catch({});
            // navigate(
            //   `/q/${params.username}/member/student/admission/profile`,
            //   {
            //     state: {
            //       sid: location?.student?._id,
            //       viewAs: "STUDENT",
            //       instituteId: location?.student?.institute?._id,
            //       feeStructureId:location?.student?.fee_structure
            //     },
            //   }
            // );
          } else {
            studentEditProfileAboutDetailRefetch();
            setDisabled((pre) => !pre);
            navigate(-1);
          }
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  const onRadioChange = (e, flow) => {
    // console.info("onRadioChange", flow, e.target.value);
    setState((prev) => ({
      ...prev,
      [flow]: e.target.value,
    }));
  };
  // console.info("stateLabel", stateLabel);

  return (
    <>
      <form className={style.fc_container} onSubmit={onPreview}>
        {stateLabel?.length > 0 ? null : <Loading />}
        {stateLabel?.map((sl, index) =>
          index === 0 ? (
            <section
              className={style.fcc_section}
              key={`${sl?.section_name}${index}`}
            >
              <div
                className={style.fccs_inner}
                style={{
                  justifyContent: "flex-end",
                  marginTop: "1rem",
                }}
              >
                {sl?.field_list?.map((fd, d_index) => (
                  <div className={style.fcci_container}>
                    <label
                      htmlFor={`${fd?.db_key}${d_index}`}
                      className={style.fcc_label}
                    >
                      <img
                        className={style.fcc_photo}
                        src={
                          state[fd?.db_key]
                            ? `${imageShowUrl}/${state[fd?.db_key]}`
                            : `${assestsFeedUrl}/feed-user-avatar.svg`
                        }
                        alt="student avatar"
                      />
                      <img
                        src="/images/user-pencil-edit-transparent.svg"
                        alt="edit icon"
                        className={style.fcce_icon}
                      />
                    </label>
                    <input
                      type="file"
                      id={`${fd?.db_key}${d_index}`}
                      onChange={(e) => onCropFileUpload(e, fd?.db_key)}
                      style={{ display: "none" }}
                    />
                    <p>
                      {fd?.label ?? ""}
                      {errorField[fd?.db_key] ? (
                        <span
                          style={{
                            color: "rgb(231, 75, 75)",
                          }}
                        >
                          * {t("required")}
                        </span>
                      ) : null}
                    </p>
                  </div>
                ))}
              </div>
            </section>
          ) : sl?.section_key === "academic_details" ? (
            <section
              className={style.fcc_section}
              key={`${sl?.section_name}${index}`}
            >
              <h4>{sl?.section_name}:</h4>
              <section
                style={{
                  paddingBottom: "0.5rem",
                }}
              >
                {sl?.field_list?.map((fl, f_index) => (
                  <Fragment key={`${fl?.db_key}${f_index}`}>
                    <CustomCheckbox
                      label={fl?.label}
                      options={fl?.options}
                      defaultValue={state[fl?.db_key] === "Yes" ? true : false}
                      onChange={(e) => onAcademicCheckbox(e, fl?.db_key)}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                        fontWeight: "500",
                        marginBottom: "-1rem",
                      }}
                      isCheck
                      isOptionError
                    />
                    {state[fl?.db_key] === "Yes" ? (
                      <>
                        {[
                          "std_tenth_details",
                          "hsc_diploma",
                          "fyjc",
                          "fy_sem_I",
                          "fy_sem_II",
                          "sy_sem_III",
                          "sy_sem_IV",
                          "ty_sem_V",
                          "ty_sem_VI",
                          "msc_mcom",
                        ]?.includes(fl?.db_key) ? (
                          <section
                            style={{
                              display: "flex",
                              flexWrap: "wrap",
                              alignItems: "center",
                              justifyContent: "flex-start",
                              gap: "2%",
                              paddingLeft: "2.2rem",
                              marginTop: "-1rem",
                            }}
                          >
                            {fl?.nested_form_checklist?.map((gl, g_index) =>
                              gl?.form_checklist_typo === "TEXT" ? (
                                <FormInput
                                  key={`${gl?.form_checklist_key}${g_index}`}
                                  label={gl?.form_checklist_name}
                                  placeholder={gl?.form_checklist_placeholder}
                                  name={gl?.form_checklist_key}
                                  type={gl?.form_checklist_typo}
                                  value={state[gl?.form_checklist_key]}
                                  onChange={onChange}
                                  errorShow={errorField[gl?.form_checklist_key]}
                                  customFormInputContainer={{
                                    width: gl?.width ? `${gl?.width}` : "49%",
                                  }}
                                />
                              ) : gl?.form_checklist_typo === "NUMBER" ? (
                                <FormInput
                                  key={`${gl?.form_checklist_name}${g_index}`}
                                  label={gl?.form_checklist_name}
                                  placeholder={gl?.form_checklist_placeholder}
                                  name={gl?.form_checklist_key}
                                  type={gl?.form_checklist_typo}
                                  value={state[gl?.form_checklist_key]}
                                  onChange={onNumberInputChange}
                                  errorShow={errorField[gl?.form_checklist_key]}
                                  customFormInputContainer={{
                                    width: gl?.width ? `${gl?.width}` : "49%",
                                  }}
                                />
                              ) : null
                            )}
                          </section>
                        ) : (
                          fl?.nested_form_checklist?.map((gl, g_index) => (
                            <section
                              key={`${gl?.form_checklist_name}${g_index}`}
                              className={style.fcc_section}
                              style={{
                                paddingLeft: "2.2rem",
                              }}
                            >
                              <CustomCheckbox
                                label=""
                                options={gl?.form_checklist_typo_option_pl}
                                defaultValue={
                                  state[gl?.form_checklist_key] === "Yes"
                                    ? true
                                    : false
                                }
                                onChange={(e) =>
                                  onAcademicCheckbox(e, gl?.form_checklist_key)
                                }
                                errorShow={errorField[gl?.form_checklist_key]}
                                customFormInputContainer={{
                                  width: gl?.width ? `${gl?.width}` : "49%",
                                  fontWeight: "500",
                                }}
                                isCheck
                              />

                              {state[gl?.form_checklist_key] === "Yes" ? (
                                <section
                                  style={{
                                    display: "flex",
                                    flexWrap: "wrap",
                                    alignItems: "center",
                                    justifyContent: "flex-start",
                                    gap: "2%",
                                    paddingLeft: "2.2rem",
                                    marginTop: "-1rem",
                                  }}
                                >
                                  {gl?.nested_form_checklist_nested?.map(
                                    (jl, j_index) =>
                                      jl?.form_checklist_typo === "TEXT" ? (
                                        <FormInput
                                          key={`${jl?.form_checklist_key}${j_index}`}
                                          label={jl?.form_checklist_name}
                                          placeholder={
                                            jl?.form_checklist_placeholder
                                          }
                                          name={jl?.form_checklist_key}
                                          type={jl?.form_checklist_typo}
                                          value={state[jl?.form_checklist_key]}
                                          onChange={onChange}
                                          // errorShow={errorField[jl?.form_checklist_key]}
                                          customFormInputContainer={{
                                            width: jl?.width
                                              ? `${jl?.width}`
                                              : "49%",
                                          }}
                                        />
                                      ) : jl?.form_checklist_typo ===
                                        "NUMBER" ? (
                                        <FormInput
                                          key={`${jl?.form_checklist_name}${j_index}`}
                                          label={jl?.form_checklist_name}
                                          placeholder={
                                            jl?.form_checklist_placeholder
                                          }
                                          name={jl?.form_checklist_key}
                                          type={jl?.form_checklist_typo}
                                          value={state[jl?.form_checklist_key]}
                                          onChange={onNumberInputChange}
                                          // errorShow={errorField[jl?.form_checklist_key]}
                                          customFormInputContainer={{
                                            width: jl?.width
                                              ? `${jl?.width}`
                                              : "49%",
                                          }}
                                        />
                                      ) : null
                                  )}
                                </section>
                              ) : null}
                            </section>
                          ))
                        )}
                      </>
                    ) : null}
                  </Fragment>
                ))}
              </section>
              <BorderBottom />
            </section>
          ) : (
            <section
              className={style.fcc_section}
              key={`${sl?.section_name}${index}`}
            >
              <h4>{sl?.section_name}:</h4>
              <div className={style.fccs_inner}>
                {sl?.field_list?.map((fl, f_index) =>
                  fl?.type === "TEXT" ? (
                    <Fragment key={`${fl?.db_key}${f_index}`}>
                      {fl?.form_common_key ? (
                        <>
                          {state[fl?.form_common_key] === "Yes" ? (
                            <FormInput
                              key={`${fl?.db_key}${f_index}`}
                              label={fl?.label}
                              placeholder={fl?.placeholder}
                              name={fl?.db_key}
                              type={fl?.type}
                              value={state[fl?.db_key]}
                              onChange={onChange}
                              errorShow={errorField[fl?.db_key]}
                              customFormInputContainer={{
                                width: fl?.width ? `${fl?.width}` : "49%",
                              }}
                            />
                          ) : null}
                        </>
                      ) : (
                        <FormInput
                          key={`${fl?.db_key}${f_index}`}
                          label={fl?.label}
                          placeholder={fl?.placeholder}
                          name={fl?.db_key}
                          type={fl?.type}
                          value={state[fl?.db_key]}
                          onChange={onChange}
                          errorShow={errorField[fl?.db_key]}
                          customFormInputContainer={{
                            width: fl?.width ? `${fl?.width}` : "49%",
                          }}
                        />
                      )}
                    </Fragment>
                  ) : fl?.type === "TEXTAREA" ? (
                    <FormTextarea
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      placeholder={fl?.placeholder}
                      name={fl?.db_key}
                      type={fl?.type}
                      value={state[fl?.db_key]}
                      onChange={onChange}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: "100%",
                      }}
                    />
                  ) : fl?.type === "NUMBER" ? (
                    <FormInput
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      placeholder={fl?.placeholder}
                      name={fl?.db_key}
                      type={fl?.type}
                      value={state[fl?.db_key]}
                      onChange={onChange}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "SELECT" ? (
                    <CustomSelect
                      key={`${fl?.db_key}${f_index}`}
                      selectLabel={fl?.label}
                      selectedValue={fl?.placeholder}
                      defalutValue={state[fl?.db_key]}
                      onClick={(val) => onSelectDropdown(val, fl?.db_key)}
                      options={fl?.options || []}
                      errorShow={errorField[fl?.db_key]}
                      customStyleContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                      customListStyle={{
                        position: "absolute",
                        width: "100%",
                        zIndex: "1",
                      }}
                    />
                  ) : fl?.type === "MULITSELECT" ? (
                    <CustomSelect
                      key={`${fl?.db_key}${f_index}`}
                      selectLabel={fl?.label}
                      selectedValue={fl?.placeholder}
                      defalutValue={state[fl?.db_key]}
                      onClick={(val) => onSelectDropdown(val, fl?.db_key)}
                      options={fl?.options || []}
                      errorShow={errorField[fl?.db_key]}
                      customStyleContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                      customListStyle={{
                        position: "absolute",
                        width: "100%",
                        zIndex: "1",
                      }}
                    />
                  ) : fl?.type === "RADIO" ? (
                    <CustomRadioButton
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      options={fl?.options}
                      defaultValue={state[fl?.db_key]}
                      onChange={(e) => onRadioChange(e, fl?.db_key)}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "CHECKBOX" ? (
                    <CustomCheckbox
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      options={fl?.options}
                      defaultValue={state[fl?.db_key]}
                      onChange={(e) => onCheckbox(e, fl?.db_key)}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                      isCheck
                    />
                  ) : fl?.type === "CALENDAR" ? (
                    <FormCalendarInput
                      key={`${fl?.db_key}${f_index}`}
                      label={fl?.label}
                      placeholder={fl?.placeholder}
                      name={fl?.db_key}
                      type={fl?.type}
                      value={
                        state[fl?.db_key]
                          ? slashDateFormat(state[fl?.db_key])
                          : ""
                      }
                      onChange={(e) => onCalendarChoose(e, fl?.db_key)}
                      errorShow={errorField[fl?.db_key]}
                      customFormInputContainer={{
                        width: fl?.width ? `${fl?.width}` : "49%",
                      }}
                    />
                  ) : fl?.type === "FILE" ? (
                    <Fragment key={`${fl?.db_key}${f_index}`}>
                      {fl?.form_common_key ? (
                        <>
                          {state[fl?.form_common_key] === "Yes" ? (
                            <FormFile
                              label={fl?.label}
                              placeholder={fl?.placeholder}
                              name={fl?.db_key}
                              type={fl?.type}
                              value={
                                state[fl?.db_key]
                                  ? {
                                      key: state[fl?.db_key],
                                    }
                                  : ""
                              }
                              onChange={(e) => onUploadDocument(e, fl?.db_key)}
                              errorShow={errorField[fl?.db_key]}
                              customFormFileContainer={{
                                width: fl?.width ? `${fl?.width}` : "49%",
                              }}
                            />
                          ) : null}
                        </>
                      ) : (
                        <FormFile
                          label={fl?.label}
                          placeholder={fl?.placeholder}
                          name={fl?.db_key}
                          type={fl?.type}
                          value={
                            state[fl?.db_key]
                              ? {
                                  key: state[fl?.db_key],
                                }
                              : ""
                          }
                          onChange={(e) => onUploadDocument(e, fl?.db_key)}
                          errorShow={errorField[fl?.db_key]}
                          customFormFileContainer={{
                            width: fl?.width ? `${fl?.width}` : "49%",
                          }}
                        />
                      )}
                    </Fragment>
                  ) : fl?.type === "CROPIMAGE" ? (
                    <Fragment key={`${fl?.db_key}${f_index}`}>
                      {fl?.form_checklist_key ===
                      "studentProfilePhoto" ? null : (
                        <FormFile
                          label={fl?.label}
                          placeholder={fl?.placeholder}
                          name={fl?.db_key}
                          type={fl?.type}
                          value={
                            state[fl?.db_key]
                              ? {
                                  key: state[fl?.db_key],
                                }
                              : ""
                          }
                          onChange={(e) => onCropFileUpload(e, fl?.db_key)}
                          errorShow={errorField[fl?.db_key]}
                          customFormFileContainer={{
                            width: fl?.width ? `${fl?.width}` : "49%",
                          }}
                        />
                      )}
                    </Fragment>
                  ) : null
                )}
              </div>
              <BorderBottom />
            </section>
          )
        )}
        <FormCreateButton
          label="update"
          customStyle={{
            width: "100%",
          }}
        />
      </form>
      {disabled && <QvipleLoader />}
      {admission && (
        <StudentAdmissionPayFees
          onClose={() => setAdmission("")}
          admission={admission}
          install={admission?.applicable_card?.remaining_array?.[0]}
          isEditable={
            getQuery.state?.student?.online_amount_edit_access === "Granted"
              ? true
              : false
          }
        />
      )}
      {openCrop && (
        <Dialog open={openCrop}>
          <CustomCrop
            setOpenCrop={setOpenCrop}
            fileUrl={fileUrl}
            first={
              ["studentProfilePhoto", "studentIdProfilePhoto"]?.includes(
                activeStep
              )
                ? 1
                : 16
            }
            second={
              ["studentProfilePhoto", "studentIdProfilePhoto"]?.includes(
                activeStep
              )
                ? 1
                : 9
            }
            onCrop={onCrop}
          />
        </Dialog>
      )}
    </>
  );
};

export default SelfStudentEditDynamicForm;
