import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import QvipleLoader from "../../optimized/Loader/QvipleLoader";
import BorderBottom from "../../optimized/Ui/Border/BorderBottom";
import CustomCheckbox from "../../optimized/Ui/CustomCheckbox/CustomCheckbox";
import { Fragment, useEffect, useState } from "react";
import FormFile from "../../optimized/Ui/Input/FormFile";
import FormCalendarInput from "../../optimized/Ui/Input/FormCalendarInput";
import CustomRadioButton from "../../optimized/Ui/CustomRadioButton/CustomRadioButton";
import CustomSelect from "../../optimized/Ui/Select/CustomSelect";
import FormTextarea from "../../optimized/Ui/Input/FormTextarea";
import Loading from "../../optimized/Loader/Loading";
import { useFileUpload } from "../../hooks/authentication-api";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../optimized/Utils/Functions/dateUtility";
import style from "../../optimized/Form/Form.module.css";
import FormInput from "../../optimized/Ui/Input/FormInput";
import FormCreateButton from "../../optimized/Ui/Button/FormCreateButton";
import {
  existingRequiredField,
  requiredField,
} from "../../optimized/Validation/functions/checkingRequiredField";
import { useStaffProfileAboutDetail } from "../../optimized/Hooks/ApiHooks/InstituteStaff/institute-staff-api";
import CreateButton from "../../optimized/Ui/Button/CreateButton";
import { useStaffFormEdit } from "../../Department/Institute/Components/DepartmentApi/depart-api";
import dayjs from "dayjs";
import CustomSelectObj from "../../optimized/Ui/Select/CustomSelectObj";
import InstituteMixedDepartmentDropdown from "../../optimized/UserStaffMember/InstituteMixedDepartment/InstituteMixedDepartmentDropdown";

const array_field = [
  "staff_qualification_details",
  "staff_research_and_publication",
  "staff_past_experience_details",
];

const insertStaffArrayField = {
  staff_qualification_details: {
    institute_name: "",
    degree_name: "",
    passing_year: "",
  },
  staff_past_experience_details: {
    institute_name: "",
    designation: "",
    from: "",
    to: "",
  },
  staff_research_and_publication: {
    title: "",
    description: "",
    link_url: "",
    attachment: "",
  },
};

const DynamicEditStaffForm = () => {
  const getQuery = useLocation();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [stateLabel, setStateLabel] = useState([]);
  const [state, setState] = useState({});
  const [dynamicState, setDynamicState] = useState({});
  const [notRequiredState, setNotRequiredState] = useState([]);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [fileUpload] = useFileUpload();
  const [staffFormEdit] = useStaffFormEdit();
  const { staffProfileAboutDetail, staffProfileAboutDetailRefetch } =
    useStaffProfileAboutDetail({
      data: {
        sid: getQuery.state?.sid,
      },
      skip: !getQuery.state?.sid,
    });

  useEffect(() => {
    if (staffProfileAboutDetail) {
      let lb = [];
      let k_lb = {};
      let dy = {};
      let rq = [];
      for (let i = 0; i < staffProfileAboutDetail?.length; i++) {
        let st = staffProfileAboutDetail[i];
        let obj = {
          section_name: st?.key,
          field_list: [],
        };
        for (let j = 0; j < st?.fields?.length; j++) {
          let ct = st?.fields[j];
          obj.field_list?.push({
            label: ct?.form_checklist_lable,
            placeholder: ct?.form_checklist_placeholder,
            type: ct?.form_checklist_typo,
            db_key: ct?.form_checklist_key,
            options: ct?.form_checklist_typo_option_pl,
            objOptions: ct?.form_checklist_typo_option_pl_staff,
            width: ct?.width,
          });
          if (ct?.form_checklist_key) {
            if (array_field?.includes(ct?.form_checklist_key)) {
              let ft = [];
              for (let dt of ct?.value) {
                ft.push({ ...dt });
              }
              k_lb[ct?.form_checklist_key] = ft;
            } else {
              if (ct?.form_checklist_key === "staffJoinDate") {
                k_lb[ct?.form_checklist_key] = dayjs(ct?.value).format(
                  "DD/MM/YYYY"
                );
              } else {
                k_lb[ct?.form_checklist_key] = ct?.value;
              }
            }
          }
          if (ct?.form_checklist_key_status === "DYNAMIC") {
            dy[ct?.form_checklist_key] = ct?.value;
          }
          // if (!ct?.form_checklist_required) {
          rq.push(ct?.form_checklist_key);
          // }
        }
        lb.push(obj);
      }

      setStateLabel(lb);
      setState(k_lb);
      setDynamicState(dy);
      setNotRequiredState(rq);
    }
  }, [staffProfileAboutDetail]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };
  const onSelectObjDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };
  const onUploadDocument = (e, flow) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            [flow]: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onCalendarChoose = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(e),
    }));
  };

  const onAddArrayField = (flow, otherFl) => {
    if (otherFl) {
      setState((prev) => ({
        ...prev,
        [flow]: [...prev[flow], { ...researchField[otherFl] }],
      }));
    } else {
      setState((prev) => ({
        ...prev,
        [flow]: [...prev[flow], { ...insertStaffArrayField[flow] }],
      }));
    }
  };
  const onArrayCalendarChoose = (e, subFlow, ind, flow) => {
    let inp = [...state[flow]];
    inp[ind][subFlow] = ymdSlashDateFormat(e);
    setState((prev) => ({
      ...prev,
      [flow]: inp,
    }));
  };
  const onArrayUploadDocument = (e, subFlow, ind, flow) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          let inp = [...state[flow]];
          inp[ind][subFlow] = res.data?.imageKey;
          setState((prev) => ({
            ...prev,
            [flow]: inp,
          }));
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onArrayChange = (e, ind, flow) => {
    let inp = [...state[flow]];
    inp[ind][e.target.name] = e.target.value;
    setState((prev) => ({
      ...prev,
      [flow]: inp,
    }));
  };

  const onPaperSelectDepartment = (dt, ind, flow) => {
    let inp = [...state[flow]];
    inp[ind]["department"] = dt?._id;
    inp[ind]["department_name"] = dt?.dName;
    setState((prev) => ({
      ...prev,
      [flow]: inp,
    }));
  };

  const onPreview = (e) => {
    e.preventDefault();
    let validation = requiredField(state, notRequiredState);
    if (existingRequiredField(validation) && getQuery.state?.sid) {
      setDisabled((pre) => !pre);
      const editProfile = {
        staff: {
          ...state,
          staff_department: state?.staff_department?._id
            ? state?.staff_department?._id
            : null,
        },
        fileArray: [],
        staff_dynamic_field: [],
      };
      for (let ob in dynamicState) {
        editProfile.staff_dynamic_field.push({
          key: ob,
          value: state[ob],
        });
      }

      staffFormEdit({
        sid: getQuery.state?.sid,
        editProfile,
      })
        .then(() => {
          staffProfileAboutDetailRefetch();
          setDisabled((pre) => !pre);
          navigate(-1);
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  // console.info(
  //   "studentApplyFormFieldDetail",
  //   state
  //   // activeStep
  //   // notRequiredState
  //   // stateLabel
  // );

  return (
    <>
      <form className={style.fc_container} onSubmit={onPreview}>
        {stateLabel?.length > 0 ? null : <Loading />}
        {stateLabel?.map((sl, index) => (
          <section
            className={style.fcc_section}
            key={`${sl?.section_name}${index}`}
          >
            <h4>{sl?.section_name}:</h4>
            <div className={style.fccs_inner}>
              {sl?.field_list?.map((fl, f_index) =>
                fl?.type === "TEXT" ? (
                  <FormInput
                    key={`${fl?.db_key}${f_index}`}
                    label={fl?.label}
                    placeholder={fl?.placeholder}
                    name={fl?.db_key}
                    type={fl?.type}
                    value={state[fl?.db_key]}
                    onChange={onChange}
                    errorShow={errorField[fl?.db_key]}
                    customFormInputContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                  />
                ) : fl?.type === "TEXTAREA" ? (
                  <FormTextarea
                    key={`${fl?.db_key}${f_index}`}
                    label={fl?.label}
                    placeholder={fl?.placeholder}
                    name={fl?.db_key}
                    type={fl?.type}
                    value={state[fl?.db_key]}
                    onChange={onChange}
                    errorShow={errorField[fl?.db_key]}
                    customFormInputContainer={{
                      width: "100%",
                    }}
                  />
                ) : fl?.type === "NUMBER" ? (
                  <FormInput
                    key={`${fl?.db_key}${f_index}`}
                    label={fl?.label}
                    placeholder={fl?.placeholder}
                    name={fl?.db_key}
                    type={fl?.type}
                    value={state[fl?.db_key]}
                    onChange={onChange}
                    errorShow={errorField[fl?.db_key]}
                    customFormInputContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                  />
                ) : fl?.type === "SELECT" ? (
                  <CustomSelect
                    key={`${fl?.db_key}${f_index}`}
                    selectLabel={fl?.label}
                    selectedValue={fl?.placeholder}
                    defalutValue={state[fl?.db_key]}
                    onClick={(val) => onSelectDropdown(val, fl?.db_key)}
                    options={fl?.options || []}
                    errorShow={errorField[fl?.db_key]}
                    customStyleContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                    customListStyle={{
                      position: "absolute",
                      width: "100%",
                      zIndex: "1",
                    }}
                  />
                ) : fl?.type === "SELECT_DEPARTMENT" ? (
                  <CustomSelectObj
                    key={`${fl?.db_key}${f_index}`}
                    selectLabel={fl?.label}
                    selectedValue={fl?.placeholder}
                    defalutValue={state[fl?.db_key]?.dName}
                    onClick={(val) => onSelectObjDropdown(val, fl?.db_key)}
                    options={fl?.objOptions || []}
                    errorShow={errorField[fl?.db_key]}
                    customStyleContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                    customListStyle={{
                      position: "absolute",
                      width: "100%",
                      zIndex: "1",
                    }}
                  />
                ) : fl?.type === "MULITSELECT" ? (
                  <CustomSelect
                    key={`${fl?.db_key}${f_index}`}
                    selectLabel={fl?.label}
                    selectedValue={fl?.placeholder}
                    defalutValue={state[fl?.db_key]}
                    onClick={(val) => onSelectDropdown(val, fl?.db_key)}
                    options={fl?.options || []}
                    errorShow={errorField[fl?.db_key]}
                    customStyleContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                    customListStyle={{
                      position: "absolute",
                      width: "100%",
                      zIndex: "1",
                    }}
                  />
                ) : fl?.type === "RADIO" ? (
                  <CustomRadioButton
                    key={`${fl?.db_key}${f_index}`}
                    label={fl?.label}
                    options={["Java", "C++", "C", "C#"]}
                    value={state[fl?.db_key]}
                    onChange={onChange}
                    errorShow={errorField[fl?.db_key]}
                    customFormInputContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                  />
                ) : fl?.type === "CHECKBOX" ? (
                  <CustomCheckbox
                    key={`${fl?.db_key}${f_index}`}
                    label={fl?.label}
                    options={fl?.options}
                    defaultValue={state[fl?.db_key]}
                    onChange={onChange}
                    errorShow={errorField[fl?.db_key]}
                    customFormInputContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                  />
                ) : fl?.type === "CALENDAR" ? (
                  <FormCalendarInput
                    key={`${fl?.db_key}${f_index}`}
                    label={fl?.label}
                    placeholder={fl?.placeholder}
                    name={fl?.db_key}
                    type={fl?.type}
                    value={slashDateFormat(state[fl?.db_key])}
                    onChange={(e) => onCalendarChoose(e, fl?.db_key)}
                    errorShow={errorField[fl?.db_key]}
                    customFormInputContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                  />
                ) : fl?.type === "FILE" ? (
                  <FormFile
                    key={`${fl?.db_key}${f_index}`}
                    label={fl?.label}
                    placeholder={fl?.placeholder}
                    name={fl?.db_key}
                    type={fl?.type}
                    value={
                      state[fl?.db_key]
                        ? {
                            key: state[fl?.db_key],
                          }
                        : ""
                    }
                    onChange={(e) => onUploadDocument(e, fl?.db_key)}
                    errorShow={errorField[fl?.db_key]}
                    customFormFileContainer={{
                      width: fl?.width ? `${fl?.width}` : "49%",
                    }}
                  />
                ) : fl?.type === "CROPIMAGE" ? (
                  <Fragment key={`${fl?.db_key}${f_index}`}>
                    {fl?.form_checklist_key === "studentProfilePhoto" ? null : (
                      <FormFile
                        label={fl?.label}
                        placeholder={fl?.placeholder}
                        name={fl?.db_key}
                        type={fl?.type}
                        value={
                          state[fl?.db_key]
                            ? {
                                key: state[fl?.db_key],
                              }
                            : ""
                        }
                        onChange={onChange}
                        errorShow={errorField[fl?.db_key]}
                        customFormFileContainer={{
                          width: fl?.width ? `${fl?.width}` : "49%",
                        }}
                      />
                    )}
                  </Fragment>
                ) : fl?.type === "BUTTON" ? (
                  <Fragment key={`${fl?.db_key}${f_index}`}>
                    {fl?.db_key === "staff_qualification_details" &&
                      state[fl?.db_key]?.map((st, index) => (
                        <div
                          key={`${state[fl?.db_key]}${index}ty`}
                          className={style.fccs_inner}
                        >
                          <FormInput
                            label="Institute Name"
                            placeholder="Enter Institute name"
                            name="institute_name"
                            type="text"
                            value={st?.institute_name}
                            onChange={(e) =>
                              onArrayChange(e, index, fl?.db_key)
                            }
                            customFormInputContainer={{
                              width: "49%",
                            }}
                          />
                          <FormInput
                            label="Degree Name"
                            placeholder="Enter Degree name"
                            name="degree_name"
                            type="text"
                            value={st?.degree_name}
                            onChange={(e) =>
                              onArrayChange(e, index, fl?.db_key)
                            }
                            customFormInputContainer={{
                              width: "49%",
                            }}
                          />
                          <FormInput
                            label="Passing Year"
                            placeholder="Enter Passing year"
                            name="passing_year"
                            type="text"
                            value={st?.passing_year}
                            onChange={(e) =>
                              onArrayChange(e, index, fl?.db_key)
                            }
                            customFormInputContainer={{
                              width: "49%",
                            }}
                          />
                        </div>
                      ))}
                    {fl?.db_key === "staff_past_experience_details" &&
                      state[fl?.db_key]?.map((st, index) => (
                        <div
                          key={`${state[fl?.db_key]}${index}ty`}
                          className={style.fccs_inner}
                        >
                          <FormInput
                            label="Institute Name"
                            placeholder="Enter Institute name"
                            name="institute_name"
                            type="text"
                            value={st?.institute_name}
                            onChange={(e) =>
                              onArrayChange(e, index, fl?.db_key)
                            }
                            customFormInputContainer={{
                              width: "49%",
                            }}
                          />
                          <FormInput
                            label="Designation"
                            placeholder="Enter Designation"
                            name="designation"
                            type="text"
                            value={st?.designation}
                            onChange={(e) =>
                              onArrayChange(e, index, fl?.db_key)
                            }
                            customFormInputContainer={{
                              width: "49%",
                            }}
                          />
                          <FormCalendarInput
                            label="From"
                            placeholder="Select Date"
                            name="from"
                            value={st?.from ? slashDateFormat(st?.from) : ""}
                            onChange={(e) =>
                              onArrayCalendarChoose(
                                e,
                                "from",
                                index,
                                fl?.db_key
                              )
                            }
                            customFormInputContainer={{
                              width: "49%",
                            }}
                          />
                          <FormCalendarInput
                            label="To"
                            placeholder="Select Date"
                            name="to"
                            value={st?.to ? slashDateFormat(st?.to) : ""}
                            onChange={(e) =>
                              onArrayCalendarChoose(e, "to", index, fl?.db_key)
                            }
                            customFormInputContainer={{
                              width: "49%",
                            }}
                          />
                        </div>
                      ))}
                    {fl?.db_key === "staff_research_and_publication" &&
                      state[fl?.db_key]?.map((st, index) =>
                        st?.add_type === "PAPER" ? (
                          <div
                            key={`${state[fl?.db_key]}${index}ty`}
                            className={style.fccs_inner}
                            style={{
                              borderBottom: "0.5px solid rgba(0, 0, 0, 0.15)",
                              paddingBottom: "0.5rem",
                              marginBottom: "0.5rem",
                            }}
                          >
                            <FormInput
                              label="Title"
                              placeholder="Please enter title of paper"
                              name="title_of_paper"
                              type="text"
                              value={st?.title_of_paper}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="Name of the Author's"
                              placeholder="Please enter title of paper"
                              name="author_name"
                              type="text"
                              value={st?.author_name}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <InstituteMixedDepartmentDropdown
                              selectLabel="Department of the teacher"
                              onSelect={(gt) =>
                                onPaperSelectDepartment(gt, index, fl?.db_key)
                              }
                              instituteId={getQuery.state?.instituteId}
                              defalutValue={st?.department_name}
                              customStyleContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="Name of Journal"
                              placeholder="Please enter name of journal"
                              name="journal_name"
                              type="text"
                              value={st?.journal_name}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="Year"
                              placeholder="Please enter reaserch publication year"
                              name="publication_year"
                              type="text"
                              value={st?.publication_year}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="ISSN Number"
                              placeholder="Please enter issn number"
                              name="issn_number"
                              type="text"
                              value={st?.issn_number}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="Link"
                              placeholder="Please enter recognition link"
                              name="link_recognition"
                              type="text"
                              value={st?.link_recognition}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                          </div>
                        ) : (
                          <div
                            key={`${state[fl?.db_key]}${index}ty`}
                            className={style.fccs_inner}
                            style={{
                              borderBottom: "0.5px solid rgba(0, 0, 0, 0.15)",
                              paddingBottom: "0.5rem",
                              marginBottom: "0.5rem",
                            }}
                          >
                            <FormInput
                              label="Title"
                              placeholder="Please enter title of book / chapters published"
                              name="title_of_book"
                              type="text"
                              value={st?.title_of_book}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="Title of the Paper"
                              placeholder="Please enter title of paper"
                              name="title_of_paper"
                              type="text"
                              value={st?.title_of_paper}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="Title of the Proceedings of the Conference"
                              placeholder="Please enter Title of the proceedings of the conference"
                              name="title_of_preceeding"
                              type="text"
                              value={st?.title_of_preceeding}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="Name of the Conference"
                              placeholder="Please enter name of the conference"
                              name="conference_name"
                              type="text"
                              value={st?.conference_name}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="National / International"
                              placeholder="Please enter national / international"
                              name="nation_international"
                              type="text"
                              value={st?.nation_international}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />

                            <FormInput
                              label="Year"
                              placeholder="Please enter book published year"
                              name="publication_year"
                              type="text"
                              value={st?.publication_year}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="ISBN/ISSN Number"
                              placeholder="Please enter ISBN/ISSN number of the proceeding"
                              name="issn_number"
                              type="text"
                              value={st?.issn_number}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                            <FormInput
                              label="Name of the Publisher"
                              placeholder="Please enter name of the publisher"
                              name="publisher_name"
                              type="text"
                              value={st?.publisher_name}
                              onChange={(e) =>
                                onArrayChange(e, index, fl?.db_key)
                              }
                              customFormInputContainer={{
                                width: "49%",
                              }}
                            />
                          </div>
                        )
                      )}
                    {fl?.db_key === "staff_research_and_publication" ? (
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "flex-start",
                          gap: "1rem",
                          marginTop: "1rem",
                        }}
                      >
                        <CreateButton
                          label="add_resarch_publication"
                          customStyle={{
                            paddingBlock: "0.2rem",
                            margin: "0",
                            marginTop: "0.4rem",
                          }}
                          onAction={() => onAddArrayField(fl?.db_key, "PAPER")}
                        />

                        <CreateButton
                          label="add_book_published"
                          customStyle={{
                            paddingBlock: "0.2rem",
                            margin: "0",
                            marginTop: "0.4rem",
                          }}
                          onAction={() => onAddArrayField(fl?.db_key, "BOOK")}
                        />
                      </div>
                    ) : (
                      <CreateButton
                        label="add"
                        customStyle={{
                          paddingBlock: "0.2rem",
                          margin: "0",
                          marginTop: "0.4rem",
                        }}
                        onAction={() => onAddArrayField(fl?.db_key)}
                      />
                    )}
                  </Fragment>
                ) : null
              )}
            </div>
            <BorderBottom />
          </section>
        ))}
        <FormCreateButton
          label="update"
          customStyle={{
            width: "100%",
          }}
        />
      </form>
      {disabled && <QvipleLoader />}
    </>
  );
};

