import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import QvipleLoader from "../../optimized/Loader/QvipleLoader";
import BorderBottom from "../../optimized/Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import Loading from "../../optimized/Loader/Loading";
import style from "../../optimized/Form/Form.module.css";
import {
  existingRequiredField,
  requiredField,
} from "../../optimized/Validation/functions/checkingRequiredField";
import FormCreateButton from "../../optimized/Ui/Button/FormCreateButton";
import { Notfication } from "../../validation/Snackbar";
import GroupCustomCheckboxItem from "../../optimized/Ui/CustomCheckbox/GroupCustomCheckboxItem";
import GroupCustomCheckbox from "../../optimized/Ui/CustomCheckbox/GroupCustomCheckbox";
import { useStudentApplyFormFieldDetail } from "../../optimized/Hooks/ApiHooks/InstituteStudent/institute-student-api";
import { useStudentAdmissionFormChangeSubject } from "../../optimized/Hooks/ApiHooks/StudentProfile/student-manager-api";
const DynamicEditStudentChangeSubjectForm = ({ setPreview }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [stateLabel, setStateLabel] = useState([]);
  const [state, setState] = useState({});
  const [notRequiredState, setNotRequiredState] = useState([]);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [extra, setExtra] = useState({});
  const [errorNotify, setErrorNotify] = useState({
    run: false,
    msg: "",
  });

  const [studentAdmissionFormChangeSubject] =
    useStudentAdmissionFormChangeSubject();
  // here only for application flow
  const { studentApplyFormFieldDetail } = useStudentApplyFormFieldDetail({
    data: {
      flow: "APPLICATION",
      flowId: getQuery.state?.subjectData?.appId,
      apk: "WEB",
      aid: "",
    },
    skip: !getQuery.state?.subjectData?.appId,
  });

  useEffect(() => {
    if (studentApplyFormFieldDetail) {
      let lb = [];
      let k_lb = {};
      let rq = [];
      for (let i = 0; i < studentApplyFormFieldDetail?.length; i++) {
        let st = studentApplyFormFieldDetail[i];
        if (st?.section_key === "subject_criteria") {
          let obj = {
            section_name: st?.section_name,
            section_key: st?.section_key,
            section_value: st?.section_value,
            field_list: [],
            nested_section: st?.nested_section,
          };
          for (let j = 0; j < st?.form_checklist?.length; j++) {
            let ct = st?.form_checklist[j];
            obj.field_list?.push({
              label: ct?.form_checklist_lable,
              placeholder: ct?.form_checklist_placeholder,
              type: ct?.form_checklist_typo,
              db_key: ct?.form_checklist_key,
              options: ct?.form_checklist_typo_option_pl,
              width: ct?.width,
              nested_form_checklist: ct?.nested_form_checklist ?? [],
              form_common_key: ct?.form_common_key,
            });

            for (let dt of ct?.nested_form_checklist ?? []) {
              if (!dt?.form_checklist_required) {
                rq.push(dt?.form_checklist_key);
              }

              for (let ht of dt?.nested_form_checklist_nested) {
                if (ht?.form_checklist_key) k_lb[ht?.form_checklist_key] = "";
                if (!ht?.form_checklist_required) {
                  rq.push(ht?.form_checklist_key);
                }
              }
            }

            if (ct?.form_checklist_key) k_lb[ct?.form_checklist_key] = "";

            if (!ct?.form_checklist_required) {
              rq.push(ct?.form_checklist_key);
            }
          }
          lb.push(obj);

          k_lb["is_group_select"] = false;
        }
      }

      setStateLabel(lb);
      setState(k_lb);
      setNotRequiredState(rq);
    }
  }, [studentApplyFormFieldDetail]);

  // console.info("m,msnbdvfdsgdf", notRequiredState);
  // console.info("m,errorField", errorField);
  const onGroupCheckbox = (e, flow, ind, maxCount) => {
    setExtra((prev) => ({
      ...prev,
      [flow]: {
        selected: e.target.checked,
        whichIndex: ind,
        // max_major_subject: maxCount,
        // major_subject: [],
      },
    }));
    setState((prev) => ({
      ...prev,
      is_group_select: e.target.checked ? true : false,
    }));
  };

  const onGroupCheckboxItem = (
    e,
    parentKey,
    itemParentIndex,
    itemIndex,
    rf_data
  ) => {
    if (rf_data?.or_subject === "YES") {
      const dft = { ...extra };
      let reach_item = dft[parentKey][itemParentIndex];
      let is_inster = extra[parentKey];
      let existing = is_inster[itemParentIndex];
      let rd_count = 0;
      let rd_list = [];
      let rd_nested_count = 0;
      let rd_nested_list = [];
      if (existing?.max_major_subject < rf_data?.maxCount) {
        rd_count = existing?.max_major_subject;
        rd_list = existing?.major_subject;
      } else {
        rd_count = rf_data?.maxCount;
        rd_list = [];
      }
      if (existing?.nested_select_min < rf_data?.nestedMaxCount) {
        rd_nested_count = existing?.nested_select_min;
        rd_nested_list = existing?.nested_subject;
      } else {
        rd_nested_count = rf_data?.nestedMaxCount;
        rd_nested_list = [];
      }
      if (reach_item) {
        for (let df in reach_item) {
          let ght = reach_item[df];
          if (ght?.or_subject) {
            if (ght?.parentIndex === rf_data?.parentIndex) {
              ght.selected = false;
              if (existing?.major_subject?.includes(ght?.id)) {
                rd_list = rd_list?.filter((dt) => dt !== ght?.id);
                rd_count = rd_count + 1;
              }
              if (existing?.nested_subject?.includes(ght?.id)) {
                rd_nested_list = rd_nested_list?.filter((dt) => dt !== ght?.id);
                rd_nested_count = rd_nested_count + 1;
              }
            }
          }
        }
      }

      setExtra((prev) => ({
        ...prev,
        [parentKey]: {
          ...prev[parentKey],
          [itemParentIndex]: {
            // ...prev[parentKey][itemParentIndex],
            ...reach_item,
            min: rf_data?.min,
            max: rf_data?.max,
            max_major_subject: rd_count ?? 0,
            major_subject: rd_list,
            nested_select_min: rd_nested_count ?? 0,
            nested_subject: rd_nested_list,

            [itemIndex]: {
              selected: e.target.checked,
              id: rf_data?.masterId,
              or_subject: rf_data?.or_subject,
              parentIndex: rf_data?.parentIndex,
              parentWholeKey: rf_data?.parentWholeKey,
            },
          },
        },
      }));
    } else if (rf_data?.or_subject === "FIXED") {
      let insert_flag = true;
      let is_inster = extra[parentKey];
      let rd_count = rf_data?.maxCount;
      let rd_nested_count = rf_data?.nestedMaxCount;

      if (extra[parentKey] && extra[parentKey]) {
        for (let obj in is_inster) {
          if (obj === "selected" || obj === "whichIndex") {
          } else {
            if (
              is_inster[obj]?.or_subject === "FIXED" &&
              is_inster[obj]?.selected &&
              is_inster[obj]?.id &&
              e.target.checked
            ) {
              insert_flag = false;
            }
          }
        }
      }
      if (insert_flag) {
        let id_f = [];
        for (let gy of rf_data?.fixedSubject) {
          id_f.push(gy?._id);
        }
        setExtra((prev) => ({
          ...prev,
          [parentKey]: {
            ...prev[parentKey],

            [itemParentIndex]: {
              ...prev[parentKey][itemParentIndex],
              min: rf_data?.min,
              max: rf_data?.max,
              selected: e.target.checked,
              id: id_f,
              or_subject: rf_data?.or_subject,
              max_major_subject: rd_count ?? 0,
              major_subject: [],
              nested_select_min: rd_nested_count ?? 0,
              nested_subject: [],
            },
          },
        }));
      } else {
        setExtra((prev) => ({
          ...prev,
          [parentKey]: {
            ...prev[parentKey],
            [itemParentIndex]: {
              ...prev[parentKey][itemParentIndex],
              min: rf_data?.min,
              max: rf_data?.max,
              selected: false,
              id: [],
              or_subject: rf_data?.or_subject,
              max_major_subject: rd_count ?? 0,
              major_subject: [],
              nested_select_min: rd_nested_count ?? 0,
              nested_subject: [],
            },
          },
        }));
        setErrorNotify({
          run: true,
          msg: `You cannot select more than one`,
        });
      }
    } else {
      let insert_flag = true;
      let max_val = 0;
      let is_inster = extra[parentKey];
      let existing = is_inster[itemParentIndex];
      let rd_count = 0;
      let rd_list = [];
      let rd_nested_count = 0;
      let rd_nested_list = [];

      if (existing?.max_major_subject < rf_data?.maxCount) {
        rd_count = existing?.max_major_subject;
        rd_list = existing?.major_subject;
      } else {
        rd_count = rf_data?.maxCount;
        rd_list = [];
      }
      if (existing?.nested_select_min < rf_data?.nestedMaxCount) {
        rd_nested_count = existing?.nested_select_min;
        rd_nested_list = existing?.nested_subject;
      } else {
        rd_nested_count = rf_data?.nestedMaxCount;
        rd_nested_list = [];
      }

      if (extra[parentKey] && extra[parentKey][itemParentIndex]) {
        let is_inster = extra[parentKey][itemParentIndex];
        let i_count = 1;
        for (let obj in is_inster) {
          if (obj === "min" || obj === "max") {
          } else {
            // if (is_inster[obj]?.selected) {
            if (is_inster[obj]?.selected && is_inster[obj]?.id) {
              i_count += 1;
            }
          }
        }
        if (is_inster?.min < i_count && e.target.checked) {
          insert_flag = false;
          max_val = is_inster?.max;
        }
      }

      if (existing) {
        if (!e.target.checked && rf_data?.masterId) {
          if (rd_list?.includes(rf_data?.masterId)) {
            rd_list = rd_list?.filter((dt) => dt !== rf_data?.masterId);
            rd_count = rd_count + 1;
          }
          if (rd_nested_list?.includes(rf_data?.masterId)) {
            rd_nested_list = rd_nested_list?.filter(
              (dt) => dt !== rf_data?.masterId
            );
            rd_nested_count = rd_nested_count + 1;
          }
        }
      }
      if (insert_flag) {
        setExtra((prev) => ({
          ...prev,
          [parentKey]: {
            ...prev[parentKey],
            [itemParentIndex]: {
              ...prev[parentKey][itemParentIndex],
              min: rf_data?.min,
              max: rf_data?.max,
              max_major_subject: rd_count ?? 0,
              major_subject: rd_list,
              nested_select_min: rd_nested_count ?? 0,
              nested_subject: rd_nested_list,
              [itemIndex]: {
                selected: e.target.checked,
                id: rf_data?.masterId,
              },
            },
          },
        }));
        if (itemIndex?.length >= 4 && itemIndex?.includes("OR")) {
          if (extra && extra[parentKey] && extra[parentKey][itemParentIndex]) {
            let clear_id = extra[parentKey][itemParentIndex];
            for (let s in clear_id) {
              let o2 = clear_id[s];
              if (o2?.parentWholeKey === itemIndex) {
                o2.selected = false;
              }
            }
          }
        }
      } else {
        setExtra((prev) => ({
          ...prev,
          [parentKey]: {
            ...prev[parentKey],
            [itemParentIndex]: {
              ...prev[parentKey][itemParentIndex],
              max_major_subject: rd_count ?? 0,
              major_subject: rd_list,
              nested_select_min: rd_nested_count ?? 0,
              nested_subject: rd_nested_list,
              min: rf_data?.min,
              max: rf_data?.max,
              [itemIndex]: {
                selected: false,
                id: "",
              },
            },
          },
        }));
        setErrorNotify({
          run: true,
          msg: `You cannot select more than ${max_val}`,
        });
      }
    }
  };

  const onNestedSubject = (flow, childKey, value) => {
    let exist = extra[flow];
    let existing = exist[childKey];
    if (existing?.nested_subject?.includes(value?.sub_id)) {
      setExtra((prev) => ({
        ...prev,
        [flow]: {
          ...prev[flow],
          [childKey]: {
            ...prev[flow][childKey],
            nested_select_min: prev[flow][childKey]["nested_select_min"] + 1,
            nested_subject: prev[flow][childKey]?.nested_subject?.filter(
              (dt) => dt !== value?.sub_id
            ),
          },
        },
      }));
    } else {
      setExtra((prev) => ({
        ...prev,
        [flow]: {
          ...prev[flow],
          [childKey]: {
            ...prev[flow][childKey],
            nested_select_min: prev[flow][childKey]["nested_select_min"] - 1,
            nested_subject:
              prev[flow][childKey]?.nested_subject?.length > 0
                ? [...prev[flow][childKey]?.nested_subject, value?.sub_id]
                : [value?.sub_id],
          },
        },
      }));
    }
  };

  const onMajorSubject = (flow, childKey, value) => {
    let exist = extra[flow];
    let existing = exist[childKey];
    if (existing?.major_subject?.includes(value)) {
      setExtra((prev) => ({
        ...prev,
        [flow]: {
          ...prev[flow],
          [childKey]: {
            ...prev[flow][childKey],
            max_major_subject: prev[flow][childKey]["max_major_subject"] + 1,
            major_subject: prev[flow][childKey]?.major_subject?.filter(
              (dt) => dt !== value
            ),
          },
          // max_major_subject: prev[flow]["max_major_subject"] + 1,
          // major_subject: prev[flow]?.major_subject?.filter(
          //   (dt) => dt !== value
          // ),
        },
      }));
    } else {
      setExtra((prev) => ({
        ...prev,
        [flow]: {
          ...prev[flow],
          [childKey]: {
            ...prev[flow][childKey],
            max_major_subject: prev[flow][childKey]["max_major_subject"] - 1,
            major_subject:
              prev[flow][childKey]?.major_subject?.length > 0
                ? [...prev[flow][childKey]?.major_subject, value]
                : [value],
          },
        },
      }));
    }
  };

  const onPreview = (e) => {
    e.preventDefault();
    let validation = null;
    let c_validation = null;

    validation = requiredField(state, notRequiredState);
    c_validation = customValidation({
      args: extra,
      argsLabel: stateLabel,
    });
    if (
      existingRequiredField(validation) &&
      existingRequiredField(c_validation) &&
      getQuery.state?.sid
    ) {
      setDisabled((pre) => !pre);
      let sm_master = [];
      let mt = null;
      for (let dt of stateLabel) {
        if (dt?.section_key === "subject_criteria") {
          mt = dt;
          break;
        }
      }

      let m_subject = [];
      let nested_subject = [];
      for (let ob in extra) {
        let obj_des = extra[ob];
        if (obj_des?.selected) {
          let gp = mt?.nested_section[obj_des?.whichIndex];
          for (let i = 0; i < gp?.nested_form_checklist?.length; i++) {
            let nest_obj = gp?.nested_form_checklist[i];
            if (nest_obj?.form_checklist_typo === "TEXT") {
              sm_master.push(nest_obj?.form_checklist_subjectId);
            }
          }
          for (let ft in obj_des) {
            if (ft === "selected" || ft === "whichIndex") {
            } else {
              let gh = obj_des[ft];
              if (gh?.or_subject === "FIXED") {
                if (gh?.selected) {
                  if (gh?.id?.length > 0) {
                    sm_master.push(...gh?.id);
                  }
                  if (gh?.major_subject?.length > 0) {
                    m_subject.push(...gh?.major_subject);
                  }
                  if (gh?.nested_subject?.length > 0) {
                    nested_subject.push(...gh?.nested_subject);
                  }
                }
              } else {
                if (gh?.major_subject?.length > 0) {
                  m_subject.push(...gh?.major_subject);
                }
                if (gh?.nested_subject?.length > 0) {
                  nested_subject.push(...gh?.nested_subject);
                }
                for (let tt in gh) {
                  if (
                    [
                      "major_subject",
                      "max",
                      "min",
                      "max_major_subject",
                      "nested_select_min",
                      "nested_subject",
                    ]?.includes(tt)
                  ) {
                  } else {
                    if (gh[tt]?.selected && gh[tt]?.id) {
                      sm_master.push(gh[tt]?.id);
                    }
                  }
                }
              }
            }
          }
        }
      }

      studentAdmissionFormChangeSubject({
        sid: getQuery.state?.sid,
        bodyParameter: {
          student_optional_subject: sm_master,
          major_subject: m_subject,
          nested_subject: nested_subject,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          navigate(-1);
        })
        .catch({});
    } else {
      setErrorField({ ...validation, ...c_validation });
    }
  };

  // console.info("getQuery", state);

  const onRadioChange = (e, flow) => {
    // console.info("onRadioChange", flow, e.target.value);
    setState((prev) => ({
      ...prev,
      [flow]: e.target.value,
    }));
  };
  return (
    <>
      <form className={style.fc_container} onSubmit={onPreview}>
        {stateLabel?.length > 0 ? null : <Loading />}
        {stateLabel?.map((sl, index) => (
          <section
            className={style.fcc_section}
            key={`${sl?.section_name}${index}`}
          >
            <h4>
              {sl?.section_key === "subject_criteria"
                ? "Select Subject"
                : sl?.section_name}
              :
              {sl?.section_key === "subject_criteria" &&
              errorField["is_group_select"] ? (
                <span
                  style={{
                    color: "rgb(231, 75, 75)",
                  }}
                >
                  * {t("required")}
                </span>
              ) : null}
            </h4>
            {sl?.section_key === "subject_criteria" ? (
              <div>
                {sl?.nested_section?.map((ns, ns_index) => (
                  <section
                    className={style.fcc_section}
                    key={`${ns?.nested_section_name}}${ns_index}`}
                    style={{
                      opacity: state?.is_group_select
                        ? extra[`${ns?.nested_section_name}${ns_index}`]
                            ?.selected
                          ? "1"
                          : "0.5"
                        : "1",
                    }}
                  >
                    <GroupCustomCheckbox
                      label=""
                      options={[ns?.nested_section_name]}
                      defaultValue={
                        extra[`${ns?.nested_section_name}${ns_index}`]
                          ?.selected ?? false
                      }
                      onChange={
                        state?.is_group_select
                          ? extra[`${ns?.nested_section_name}${ns_index}`]
                              ?.selected
                            ? (e) =>
                                onGroupCheckbox(
                                  e,
                                  `${ns?.nested_section_name}${ns_index}`,
                                  ns_index,
                                  ns?.nested_major_select_max
                                )
                            : () => {}
                          : (e) =>
                              onGroupCheckbox(
                                e,
                                `${ns?.nested_section_name}${ns_index}`,
                                ns_index,
                                ns?.nested_major_select_max
                              )
                      }
                      msg={errorField[`${ns?.nested_section_name}${ns_index}`]}
                      customFormInputContainer={{
                        width: "49%",
                      }}
                      isCheck
                    />
                    <GroupCustomCheckboxItem
                      parentKey={`${ns?.nested_section_name}${ns_index}`}
                      options={ns?.nested_form_checklist}
                      defaultValue={
                        extra[`${ns?.nested_section_name}${ns_index}`]
                      }
                      onChange={onGroupCheckboxItem}
                      errorShow={errorField}
                      errorShowKey={`${ns?.nested_section_name}${ns_index}`}
                      customFormInputContainer={{
                        width: "49%",
                        paddingLeft: "2.2rem",
                      }}
                      isCheck
                      disabled={
                        !state?.is_group_select
                          ? !state?.is_group_select
                          : extra[`${ns?.nested_section_name}${ns_index}`]
                          ? false
                          : true
                      }
                      onMajorSubject={onMajorSubject}
                      onNestedSubject={onNestedSubject}
                      isGroupSelected={
                        extra[`${ns?.nested_section_name}${ns_index}`]?.selected
                      }
                    />
                  </section>
                ))}
              </div>
            ) : null}

            <BorderBottom />
          </section>
        ))}
        <FormCreateButton
          label="update"
          customStyle={{
            width: "100%",
          }}
        />
      </form>

      <Notfication
        run={errorNotify.run}
        setRun={() => setErrorNotify({ run: false, msg: "" })}
        msg={errorNotify.msg}
        postiton="bottom"
        type="error"
      />

      {disabled && <QvipleLoader />}
    </>
  );
};

export default DynamicEditStudentChangeSubjectForm;
const customValidation = ({ args, argsLabel }) => {
  const error = {};
  let mt = null;
  for (let dt of argsLabel) {
    if (dt?.section_key === "subject_criteria") {
      mt = dt;
      break;
    }
  }

  for (let ob in args) {
    let obj_des = args[ob];
    if (obj_des?.selected) {
      let gp = mt?.nested_section[obj_des?.whichIndex];
      let fd_flag = false;
      let ghj = false;
      for (let i = 0; i < gp?.nested_form_checklist?.length; i++) {
        let dt_obj = gp?.nested_form_checklist[i];
        if (dt_obj?.form_checklist_typo === "SELECT") {
          if (args && args[ob] && args[ob][i]) {
            let fg_d = args[ob][i];
            let fg_d_min = args[ob][i]["min"];
            let c = 0;
            let or = 0;
            if (fg_d?.max_major_subject) {
              error[`${ob}${i}`] = "Please select DSE subject";
            }
            if (fg_d?.nested_select_min) {
              error[`${ob}${i}`] = "Please select nested subject";
            }

            for (let dfg in fg_d) {
              let dfg_obj = fg_d[dfg];
              if (
                [
                  "major_subject",
                  "max",
                  "min",
                  "max_major_subject",
                  "nested_select_min",
                  "nested_subject",
                ]?.includes(dfg)
                // dfg === "max" ||  === "min"
              ) {
              } else if (dfg?.length >= 4 && dfg?.includes("OR")) {
                if (dfg_obj?.selected) {
                  ++c;
                  let fg = true;
                  for (let s in fg_d) {
                    let o2 = fg_d[s];
                    if (
                      [
                        "major_subject",
                        "max",
                        "min",
                        "max_major_subject",
                        "nested_select_min",
                        "nested_subject",
                      ]?.includes(s)
                    ) {
                    } else {
                      if (o2?.parentWholeKey === dfg) {
                        fg = false;
                        if (o2?.selected) {
                          break;
                        } else {
                          // error[`${ob}${i}${o2?.parentWholeKey}`] =
                          //   "Please select subject";
                        }
                      }
                    }
                  }

                  if (fg) {
                    error[`${ob}${i}${dfg}`] = "Please select subject";
                  }
                  // if (dfg_obj[or] && dfg_obj[or]["or_subject"] === "YES") {
                  //   if (dfg_obj[or]["selected"]) {
                  //     c++;
                  //   } else {
                  //     error[`${ob}${i}${`${or}OR`}`] = "Please select subject";
                  //   }
                  // } else {
                  //   error[`${ob}${i}${`${or}OR`}`] = "Please select subject";
                  // }
                }
              } else if (dfg_obj?.or_subject === "YES") {
              } else {
                if (dfg_obj?.selected && dfg_obj?.id) {
                  c++;
                }
              }
              ++or;
            }
            if (fg_d_min > c) {
              error[`${ob}${i}`] = "Please select subject";
            }
          } else {
            error[`${ob}${i}`] = "Please select subject";
          }
        } else if (dt_obj?.form_checklist_typo === "SELECT_GROUP") {
          if (args && args[ob] && args[ob][i]) {
            let fg_d = args[ob][i];
            let fg_d_selected = args[ob][i]["selected"];
            if (fg_d["or_subject"] === "FIXED") {
              if (fg_d_selected) {
                ghj = true;
                if (fg_d?.max_major_subject) {
                  error[`${ob}${i}`] = "Please select DSE subject";
                }
                if (fg_d?.nested_select_min) {
                  error[`${ob}${i}`] = "Please select nested subject";
                }
              }
            }
          } else {
          }
        } else {
        }
      }
      if (ghj) {
        fd_flag = false;
      } else {
        let gp = mt?.nested_section[obj_des?.whichIndex];
        let df = false;
        for (let i = 0; i < gp?.nested_form_checklist?.length; i++) {
          let dt_obj = gp?.nested_form_checklist[i];
          if (dt_obj?.form_checklist_typo === "SELECT_GROUP") {
            df = true;
          }
        }
        if (df) fd_flag = true;
      }
      if (fd_flag) {
        error[`${ob}`] = "Please select subject";
      }
    }
  }

  return error;
};
